@import url('https://fonts.googleapis.com/css?family=Raleway:400,100,200,300,500,600,800,700,900');
/* General */

.borderBlue {
  border: 1px solid #112648 !important
}

.borderGreen {
  border: 1px solid #adc738 !important
}

.fontRaleway {
  font-family: 'Raleway' !important;
}

.colorBlue {
  color: #112648 !important
}

.colorGreen {
  color: #adc738 !important
}

.iconGreen {
  color: #869834 !important;
}

.iconRed {
  color: #c62828 !important
}

.validatingLabel {
  font-family: monospace;
  color: red;
}

.backGround-green {
  background-color: #adc738 !important;
}

.backGround-blue {
  background-color: #112648 !important;
}

button.backGround-green:hover {
  background-color: transparent !important;
  color: #869834 !important;
}

button.backGround-blue:hover {
  background-color: transparent !important;
  color: #112648 !important;
}

.hoverGreen:hover {
  text-decoration: underline !important;
  text-decoration-color: #112648 !important;
  color: #adc738 !important;
}

.hoverBlue:hover {
  text-decoration: underline !important;
  text-decoration-color: #adc738 !important;
  color: #112648 !important;
}

.activeGreen:active {
  text-decoration: underline !important;
  text-decoration-color: #112648 !important;
  color: #adc738 !important;
}

.activeBlue:active {
  text-decoration: underline !important;
  text-decoration-color: #adc738 !important;
  color: #112648;
}

iframe {
  display: none;
}

#main {
  background-image: url(/login_background.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  /* background-attachment: fixed; */
  /* background-position: center; */
  background-origin: border-box;
  min-height: 100vh;
  /* width: 100% */
}

/* inputs */

.inputIcon {
  position: absolute;
  color: #112648;
  font-size: 22px;
  padding: 6px 5px 5px 15px
}

.inputText {
  height: 35px !important;
  width: 100% !important;
  background: #ebebeb !important;
  border: none ;
  border-radius: 7px !important;
}

input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type='checkbox'] {
  height: 20px;
  width: 20px;
  border: none !important;
  border-radius: 7px;
  background: #ebebeb !important;
}

.selectOption:hover {
  background-color: #ddd;
}

input[type='file'] {
  position: relative !important;
  z-index: 2 !important;
  text-align: right !important;
  opacity: 0 !important;
  -moz-opacity: 0 !important;
  filter: alpha(opacity=0);
}

div.fakefile {
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 1;
}

/* tables */

table tbody+thead {
  display: none;
}

thead {
  background-color: #adc738 !important;
}

thead>tr>th {
  font-weight: 700 !important;
}

td {
  color: #112648 !important;
  font-weight: 500 !important;
  vertical-align: middle !important;
  padding: 2px !important;
  text-align: center !important;
}

table {
  font-family: 'Raleway' !important;
}

/* Question Modal */

.questionModalHeader {
  background-color: #112648 !important;
}

.questionModalHeader>button {
  color: white !important;
}

.questionModalBtn {
  background-color: transparent;
  border-color: #112648;
  color: #112648;
  /* width: 130px !important; */
  height: 40px !important;
  font-weight: bold;
  border-radius: 4px;
}

.questionModalBtn:hover {
  color: white !important;
  border-color: #112648 !important;
  background-color: #112648 !important;
}

/* Invoice */

.invoiceHeader {
  -webkit-print-color-adjust: exact !important;
}

.clientAddress {
  margin-top: -30px;
}

@media print {
  .printHide {
    display: none !important;
  }
  #capture {
    margin: 0px !important;
  }
}