/*SignUp Page*/

/*form styles*/

#msform {
    position: relative;
    margin-top: 20px;
}

fieldset {
    background: white;
    border-radius: 10px;
    padding: 18px 30px;
    box-sizing: border-box;
    width: 90%;
    margin: 0 auto;
    /*stacking fieldsets above each other*/
    position: relative;
}

/*Hide all except first fieldset*/

#msform fieldset:not(:first-of-type) {
    display: none;
}

/*buttons*/

.action-button {
    width: 100px;
    background: #112648;
    font-weight: 500;
    color: white;
    border: none !important;
    border-radius: 20px;
    cursor: pointer;
    padding: 7px 5px;
    margin: 10px 5px;
    outline: none;
}

.action-button:hover {
    box-shadow: 0 0 0 2px white, 0 0 0 5px #b2c65a;
}

.action-button-previous {
    width: 100px;
    background: #b2c65a;
    font-weight: 500;
    color: white;
    border: none !important;
    border-radius: 25px;
    cursor: pointer;
    padding: 7px 5px;
    margin: 10px 5px;
    outline: none;
}

.action-button-previous:hover {
