    color: #666;
    margin-bottom: 20px;
}

/*progressbar*/

#progressbar {
    margin-bottom: 20px;
    overflow: hidden;
    /*CSS counters to number the steps*/
    counter-reset: step;
    padding: 0px
}

#progressbar li {
    list-style-type: none;
    color: #112648;
    text-transform: uppercase;
    font-size: 11px;
    font-weight: 800;
    width: 33.33%;
    float: left;
    position: relative;
    letter-spacing: 1px;
}

#progressbar li:before {
    content: counter(step);
    counter-increment: step;
    width: 48px;
    height: 48px;
    line-height: 46px;
    display: block;
    font-size: 18px;
    color: white;
    background: #b2c65a;
    border-radius: 50%;
    margin: 0 auto;
    margin-bottom: 8px;
}

/*progressbar connectors*/

#progressbar li:after {
    content: '';
    width: 100%;
    height: 3px;
    background: white;
    position: absolute;
    left: -50%;
    top: 23px;
    /*put it behind the numbers*/
    z-index: -1;
}

#progressbar li:first-child:after {
    /*connector not needed before the first step*/
    content: none;
}

/*marking active/completed steps green*/

/*The number of the step and the connector before it = blue*/

#progressbar li.active:before, #progressbar li.active:after {
    background: #112648;
    color: white;
}

#progressbar li.active {
    color: #b2c65a
}

